import React, { useState } from 'react';

export const MainContext = React.createContext({});

/**
 * @name MainProvider
 * @desc A Context wrapper to provide global data used across pages and components.
 * @param {*} param0
 */
export const MainProvider = ({ children }) => {
  const [routes] = useState([
    {
      path: '/docs/introduction',
      title: 'Introduction',
      hasChildren: [
        {
          path: '/docs/introduction/getting-started',
          title: 'Getting Started',
        },
        {
          path: '/docs/introduction/requirements',
          title: 'Requirements',
        },
        {
          path: '/docs/introduction/fundamentals',
          title: 'Fundamentals',
        },
      ],
    },
    {
      path: '/docs/customization',
      title: 'Customizations',
      hasChildren: [
        {
          path: '/docs/customizations/theming',
          title: 'Theming',
        },
      ],
    },
    {
      path: '/docs/elements',
      title: 'Elements',
      sortChildren: true,
      hasChildren: [
        {
          path: '/docs/elements/buttons',
          title: 'Buttons',
        },
        {
          path: '/docs/elements/links',
          title: 'Links',
        },
        {
          path: '/docs/elements/inputs',
          title: 'Inputs',
        },
        {
          path: '/docs/elements/icons',
          title: 'Icons',
        },
        {
          path: '/docs/elements/images',
          title: 'Images',
        },
      ],
    },
    {
      path: '/docs/components',
      title: 'Components',
      sortChildren: true,
      hasChildren: [
        {
          path: '/docs/components/form',
          title: 'Form',
        },
        {
          path: '/docs/components/navbar',
          title: 'Navbar',
        },
        {
          path: '/docs/components/card',
          title: 'Card',
        },
      ],
    },
  ]);

  // prettier-ignore
  return (
    <MainContext.Provider value={{ routes }}>
      {children}
    </MainContext.Provider>
  );
};

export default MainProvider;
